
header {
  left: 0;
  top: 0;
  position: absolute;
  z-index: 10;
  display: block;
  width: 100%;
}
.toolbar {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  color: #fff;
  background: transparent;
  padding: 4px;
  min-height: 45px;
  height: 45px;
  display: -ms-flexbox;
  display: flex;
  overflow: hidden;
  -ms-flex-direction: row;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
          flex-direction: row;
  -ms-flex-align: center;
  -webkit-box-align: center;
          align-items: center;
  -ms-flex-pack: justify;
  -webkit-box-pack: justify;
          justify-content: space-between;
  width: 100%;
  contain: content;
}
.toolbar[shadow=true] {
    box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1);
}
.toolbar .toolbar-background {
    left: 0;
    top: 0;
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    border: 0;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    pointer-events: none;
    contain: strict;
    background: #5dd5ca;
    opacity: 0;
    transition: opacity 1s;
    -webkit-transition: opacity 1s;
}
.toolbar.vert .toolbar-background {
    opacity: 1;
}
.toolbar button {
    padding: 16px;
    color: #fff;
    background: transparent;
    border: none;
}
.toolbar title {
    left: 0;
    top: 0;
    padding-left: 90px;
    padding-right: 90px;
    padding-top: 0;
    padding-bottom: 1px;
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    pointer-events: none;
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex: 1;
    -webkit-box-flex: 1;
            flex: 1;
    -ms-flex-align: center;
    -webkit-box-align: center;
            align-items: center;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
}
.toolbar title > div {
      font-size: 1.8rem;
      font-weight: 600;
      text-align: center;
      width: 100%;
}
.toolbar em, .toolbar em:after, .toolbar em:before {
    position: absolute;
    width: 22px;
    height: 0;
    left: 14px;
    top: 22px;
    border-top: solid 3px #fff;
}
.toolbar em:after, .toolbar em:before {
    content: "";
    left: 0;
    top: -10px;
}
.toolbar em:before {
    top: 4px;
}



