
ul.list {
  overflow: hidden;
  list-style-type: none;
  display: inline-block;
}
ul.list .item {
    margin-bottom: 1.33%;
    width: 49.33%;
    float: right;
    background: #fff;
    border-bottom: 1px solid rgba(0, 0, 0, 0.12);
    font-size: 12px;
}
ul.list .item:nth-child(2n) {
      float: left;
}
ul.list .item .pic {
      position: relative;
      width: 100%;
      background: #F1F1F1;
}
ul.list .item .img {
      position: relative;
      padding-bottom: 70%;
}
ul.list .item img {
      position: absolute;
      width: 100%;
}
ul.list .item .merits {
      position: absolute;
      right: 6px;
      bottom: 2px;
      left: 10px;
}
ul.list .item .merits .merit .merit-info {
        display: inline-block;
        border-radius: 3px;
        background: rgba(0, 0, 0, 0.75);
        line-height: 12px;
        padding: 3px 5px;
        max-width: 100%;
        box-sizing: border-box;
        color: #fff;
        font-size: 12px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
}
ul.list .item .box {
      padding: 5px 10px;
      height: 110px;
}
ul.list .item h3.short {
      -webkit-line-clamp: 1;
      max-height: 18px;
      min-height: 18px;
      height: 18px;
      color: #666;
}
ul.list .item .box .bt {
      clear: both;
      overflow: hidden;
}
ul.list .item .box .bt b {
        color: #0eac76;
}
ul.list .item .items-normal-m .box .bt b {
      color: #e61414;
      font-size: 20px;
      float: left;
}
ul.list .item p {
      color: #999;
      padding: 5px 0;
}
ul.list .item .bt .status {
      overflow: hidden;
      font-size: 12px;
      color: #999;
      line-height: 28px;
      height: 24px;
      white-space: nowrap;
      text-align: right;
      padding-left: 5px;
}



