.container {
  position: absolute;
  -webkit-animation: 5s hover ease infinite;
          animation: 5s hover ease infinite;
}
@-webkit-keyframes hover {
50% {
    -webkit-transform: translateY(12px);
            transform: translateY(12px);
}
}
@keyframes hover {
50% {
    -webkit-transform: translateY(12px);
            transform: translateY(12px);
}
}
.bubble1 {
  position: absolute;
  height: 8px;
  width: 8px;
  background: #E9EBE8;
  z-index: 100;
  top: 220px;
  left: 220px;
  border-radius: 100%;
  -webkit-animation: bubble 2s ease infinite;
          animation: bubble 2s ease infinite;
  -webkit-animation-delay: .3s;
          animation-delay: .3s;
  opacity: 0;
}
@-webkit-keyframes bubble {
10% {
    -webkit-transform: translate(-4px, -20px);
            transform: translate(-4px, -20px);
    opacity: 1;
}
97% {
    -webkit-transform: translate(7px, -95px);
            transform: translate(7px, -95px);
}
}
@keyframes bubble {
