
@charset "UTF-8";
/*
*add by leo zhang 20170605
*/
html {
  width: 100%;
  height: 100%;
  font-size: 62.5%;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-size-adjust: 100%;
}

/*
*隐藏滚动条
*/
::-webkit-scrollbar, *::-webkit-scrollbar {
  display: none;
}
body {
  margin: 0;
  padding: 0;
  position: fixed;
  overflow: hidden;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -webkit-user-drag: none;
  -ms-content-zooming: none;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  word-wrap: break-word;
  -webkit-text-size-adjust: none;
  -ms-text-size-adjust: none;
  text-size-adjust: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-size: 16px;
  color: #333;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
}

/*
*遮幕层
*/
.backdrop {
  -ms-touch-action: none;
      touch-action: none;
  left: 0;
  top: 0;
  position: absolute;
  z-index: 11;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0.35;
  transform: translateZ(0);
  -webkit-transform: translateZ(0);
  -moz-transform: translateZ(0);
  -o-transform: translateZ(0);
  transition: opacity 0.3s ease;
}
.backdrop.color-enter, .backdrop.color-leave-active {
    opacity: 0;
}
[padding] {
  padding: 16px;
}

/*
*滚动内容区
*/
content {
  z-index: 0;
  left: 0;
  top: 0;
  position: absolute;
  display: block;
  width: 100%;
  height: 100vh;
  contain: layout size style;
  background: #f1f1f1;
}
content .scroll-content {
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    position: absolute;
    padding-bottom: 16px;
    z-index: 1;
    display: block;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    will-change: scroll-position;
    contain: size style layout;
}
content .scroll-content::before, content .scroll-content::after {
      position: absolute;
      width: 1px;
      height: 1px;
      content: "";
}
content .scroll-content::before {
      bottom: -1px;
}
content .scroll-content::after {
      top: -1px;
}
content[has-header] {
    margin-top: 45px;
}
.willChange, .page-enter-active, .page-leave-active, .home-enter-active, .home-leave-active, .page-leave, .home-leave {
  will-change: transform,-webkit-transform,opacity;
}
.page-enter-active, .page-leave-active, .home-enter-active, .home-leave-active {
  transition: all 350ms cubic-bezier(0, 0, 0.2, 1);
}
.page-enter-active, .home-enter-active {
  transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  opacity: 1;
  filter: alpha(opacity=100);
}
.page-enter, .page-leave-active {
  transform: translate3d(100%, 0, 0);
  -webkit-transform: translate3d(100%, 0, 0);
  -moz-transform: translate3d(100%, 0, 0);
  -o-transform: translate3d(100%, 0, 0);
  opacity: 0.65;
  filter: alpha(opacity=65);
}
.page-leave, .home-leave {
  transition: all 450ms cubic-bezier(0.4, 0, 0.6, 1);
  transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  opacity: 1;
  filter: alpha(opacity=100);
}
.home-enter, .home-leave-active {
  transform: translate3d(-100%, 0, 0);
  -webkit-transform: translate3d(-100%, 0, 0);
  -moz-transform: translate3d(-100%, 0, 0);
  -o-transform: translate3d(-100%, 0, 0);
  opacity: 0.65;
  filter: alpha(opacity=65);
}



