#menu {
  width: 80%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  z-index: 80;
  display: block;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: absolute;
  contain: strict;
  will-change: transform, -webkit-transform;
  transition: -webkit-transform 0.3s cubic-bezier(0, 0, 0.2, 1);
  transition: transform 0.3s cubic-bezier(0, 0, 0.2, 1);
  transition: transform 0.3s cubic-bezier(0, 0, 0.2, 1), -webkit-transform 0.3s cubic-bezier(0, 0, 0.2, 1);
  transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
}
#menu.menu-enter, #menu.menu-leave-active {
    transform: translate3d(-100%, 0, 0);
    -webkit-transform: translate3d(-100%, 0, 0);
    -moz-transform: translate3d(-100%, 0, 0);
    -o-transform: translate3d(-100%, 0, 0);
}
#menu.menu-leave {
    transition: -webkit-transform 0.3s cubic-bezier(0.4, 0, 0.6, 1);
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.6, 1);
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.6, 1), -webkit-transform 0.3s cubic-bezier(0.4, 0, 0.6, 1);
}
#menu .menu-inner {
    background: #fff;
    left: 0;
    right: auto;
    top: 0;
    bottom: 0;
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    transform: translate3d(0, 0, 0);
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -o-transform: translate3d(0, 0, 0);
}
#menu .log-rm {
    position: relative;
    background: #f1f1f1;
    padding: 20px;
}
#menu .log-rm .btn {
    display: inline-block;
    width: 100%;
    height: 38px;
    border: 1px solid #9e9e9c;
    background: #f1f1f1;
    text-align: center;
    border-radius: 3px;
    line-height: 36px;
    color: #636463;
    pointer-events: auto;
    -webkit-transform: none;
    transform: none;
}
#menu .not-login .btn {
    width: 6.14357rem;
}
#menu .not-login .btn:active,
  #menu .not-login .btn.active {
    background: #4a90e2;
    border: 1px solid #4a90e2;
    color: #fff;
    pointer-events: auto;
}
#menu .log-rm > div {
    height: 38px;
    line-height: 38px;
    pointer-events: none;
}
#menu .log-rm .tel {
    height: auto;
    line-height: 1;
    padding-left: 56px;
    padding-bottom: 5px;
    background: url(https://static.zhongan.com/website/public/zero/v1.0/images/call_gray@2x.png) no-repeat;
    background-size: 24px auto;
    background-position: 24px center;
}
#menu .log-rm .tel span {
    display: inline-block;
    padding-top: 5px;
}
#menu .log-rm .tel span:last-child {
    letter-spacing: 3px;
    -webkit-transform: scale(0.9);
    transform: scale(0.9);
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
}
#menu list {
    position: relative;
    overflow: auto;
}
#menu .list a,
  #menu .list span {
    position: relative;
    height: 55px;
    line-height: 55px;
    display: block;
    background: #fff;
    text-indent: 50px;
    color: #636463;
    border-bottom: 1px solid #dfdede;
    font-size: 16px;
}
#menu .list span {
    height: 24px;
    line-height: 24px;
    background: #f1f1f1;
    text-indent: 20px;
    color: #b9b9b9;
    font-size: 12px;
    display: block;
}
#menu .list a:before {
    content: "";
    position: absolute;
    height: 20px;
    width: 20px;
    left: 20px;
    top: 17px;
    background: url(https://static.zhongan.com/website/public/zero/v1.0/images/menu-icons.png) 0px center no-repeat;
    background-size: 100% auto;
}
#menu .list .home:before {
    background-position: 0px -4px;
}
#menu .list .pdcenter:before {
    background-position: 0px -26px;
}
#menu .list .policySearch:before {
    background-position: 0px -49px;
}
#menu .list .claim:before {
    background-position: 0px -72px;
}
#menu .list .qrcode:before {
    background-position: 0px -95px;
}
#menu .list .feedback:before {
    background-position: 0px -118px;
}
#menu .list .about:before {
    background-position: 0px -141px;
}
#menu a {
    text-decoration: none;
}



